
































import { Component } from "vue-property-decorator";
import BaseNode from "./BaseNode.vue";
import NodeClass from "./NodeClass";
import { FreeNodeData, getBaseNode, IBaseNode } from "@tefact/core";
import { PageProperties } from "../config";

@Component({
  components: { BaseNode },
})
export default class ButtonNode extends NodeClass<IBaseNode> {
  editor: any = null;

  handleClick() {
    if (this.nodeData.link) {
      location.href = this.nodeData.link;
    }
  }

  static DEFAULT = {
    ...getBaseNode(98, 40),
    type: "ButtonNode",
    data: {
      text: "按钮",
      type: "primary",
    } as FreeNodeData,
  } as IBaseNode;

  static NODE = {
    icon: "tf-icon-mtbutton",
    type: "button",
    title: "按钮",
    nodeData: ButtonNode.DEFAULT,
  };

  static PROPERTIES = PageProperties.defaultProperties("图片配置", null, [
    {
      type: "RowsProperties",
      params: {
        title: "按钮设置",
        rows: [
          {
            type: "SelectRow",
            params: {
              title: "类型",
              path: "data.type",
              selectOptions: [
                { value: "primary", label: "Primary" },
                { value: "info", label: "Info" },
                { value: "warning", label: "Warning" },
                { value: "danger", label: "Danger" },
                { value: "text", label: "Text" },
                { value: "success", label: "Success" },
              ],
            },
          },
          {
            type: "TextRow",
            params: {
              title: "按钮文字",
              path: "data.text",
            },
          },
          {
            type: "SwitchRow",
            params: {
              title: "朴素按钮",
              path: "data.plain",
            },
          },
          {
            type: "SwitchRow",
            params: {
              title: "圆角",
              path: "data.round",
            },
          },
          {
            type: "SwitchRow",
            params: {
              title: "圆形",
              path: "data.circle",
            },
          },
        ],
      },
    },
  ]);
}
